import React from "react";
import {useState} from "react";
import Data from "./MOCK_DATA.json";

window.addEventListener("scroll", function() {
  let navArea = document.getElementById("navArea");
  
  if (window.pageYOffset > 0) {
  navArea.classList.add("is-sticky");
  } else {
  navArea.classList.remove("is-sticky");
  }
  });
  

function ForSale() {
    const[query, setQuery] = useState("")  
    return (
     <div className="center">
      <nav id="navArea">
          <input className="searchbar1" placeholder="Find Talents" 
    onChange={event => setQuery(event.target.value)}/></nav>
    <div className="hire"> 
    {
    Data.filter(function (post) {
    if (query ==='') 
    {
      return post;
    }
    else if (post.working.toLowerCase().includes(query.toLowerCase())) 
    {
      return post;
    }
  }).map((post, index) => (
          <div className="hirebox" key={index}>
            <p className="buildimg">Name : {post.first_name} 
            <img className="resim" src={post.img} height="60" width="60" alt="pic"/></p>
            <p>working at {post.working},</p>
            <p>exp : {post.exp}</p>
            <p>{post.email}</p>
            <button class="button ownbutton" id="button-5">
    <div id="translate"></div>
    <a href="#">Get Resume</a>
  </button>
          </div>
        ))
}
      </div>
      </div>
    );
  }
  export default ForSale;