import React, { useState } from 'react';
import { BsMoonFill } from "react-icons/bs";
import { FaSun } from "react-icons/fa";
import { RiZoomInFill, RiZoomOutFill} from "react-icons/ri";

export default function Settings() {
  const [isClicked, setIsClicked] = useState(false)
  const [zisClicked, setZisClicked] = useState(false)

  function myFunction() {
    let element = document.body;
    element.classList.toggle("dark");
    setIsClicked(isClicked => !isClicked);
  }

  function myFunctionZ() {
    let element = document.body;
    element.classList.toggle("zoom");
    setZisClicked(zisClicked => !zisClicked);
  }

  return (
  <>
  {/* <div className='container'>
    <div>
  <p className='themes'>Mode</p>
    <button className="dark-mode-btn" onClick={myFunction}>{isClicked ? ( <FaSun />
              ) : (
                <BsMoonFill/>
              )}</button>
    </div>
    <hr  style={{
    color: '#000',
    backgroundColor: '#000000',
    height: .01,
    borderColor : '#000000'
}}/>
    <div>
  <p className='themes'>Zoom</p>
    <button className="dark-mode-btn" onClick={myFunctionZ}>{zisClicked ? ( 
                <RiZoomOutFill />
              ) : (
                <RiZoomInFill/>
              )}</button>
    </div>
     </div> */}
  </>
  );
}