import React, { useState,useEffect } from "react";

import {
  ProSidebar,
  Menu,
  MenuItem,
  SidebarHeader,
  SidebarFooter,
  SidebarContent,
} from "react-pro-sidebar";

import {Link} from "react-router-dom";

//import icons from react icons
import { FaUser , FaHandHoldingHeart} from "react-icons/fa";
import {  FiX, FiMenu } from "react-icons/fi";
import { IoSettings, IoLogIn } from "react-icons/io5";
import { MdFindInPage} from "react-icons/md";
import { HiUserGroup } from "react-icons/hi";
import { RiUserSearchFill } from "react-icons/ri";
// import { GrMenu } from "react-icons/gr";

//import sidebar css from react-pro-sidebar module and our custom css 
import "react-pro-sidebar/dist/css/styles.css";
import "./Sidebar.css";

const Sidebar = () => {  
    //create initial menuCollapse state using useState hook
    const [menuCollapse, setMenuCollapse] = useState(true)
    const [activePage, setActivePage] = useState(true)
    useEffect(()=>{
      setActivePage(window.location.pathname);
    },[window.location.pathname])
    //create a custom function that will change menucollapse state from false to true and true to false
    const  innerIcon= () => {
        //condition checking to change state from true to false and vice versa
        activePage ? setActivePage(false) : setMenuCollapse(true);
      };
    const menuIconClick = () => {
    //condition checking to change state from true to false and vice versa
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
  };

  return (
    <>
      <div id="header">
          {/* collapsed props to change menu size using menucollapse state */}
        <ProSidebar collapsed={menuCollapse}>
          <SidebarHeader>
            <div className="closemenu" onClick={menuIconClick}>
                {/* changing menu collapse icon on click */}
              {menuCollapse ? (
                <FiMenu />
              ) : (
                <FiX/>
              )}
            </div>
          </SidebarHeader>
          <SidebarContent>
            <Menu iconShape="square">
              <MenuItem className="one" onClick={innerIcon} active={activePage === "/hire"} icon={<RiUserSearchFill />}><Link to="/hire">Hire Talent</Link></MenuItem>
              <MenuItem  className="one" onClick={innerIcon} active={activePage === "/findjob"} icon={<MdFindInPage/>}><Link to="/findjob">Find a Job</Link></MenuItem>
              <MenuItem  className="one" onClick={innerIcon}  active={activePage === "/pgs"} icon={<HiUserGroup />}><Link to="/pgs">FreeLauncer</Link></MenuItem>
              <MenuItem className="one" onClick={innerIcon} active={activePage === "/favs"} icon={<FaHandHoldingHeart />}><Link to="/favs">Mentoring</Link></MenuItem>
              <MenuItem className="one" onClick={innerIcon} active={activePage === "/settings"} icon={<IoSettings />}><Link to="/settings">Settings</Link></MenuItem>
            </Menu>
          </SidebarContent>
          <SidebarFooter>
            <Menu iconShape="square">
            <MenuItem className="two" icon={<IoLogIn />}><Link to="/login">Login</Link></MenuItem>
              <MenuItem className="two" icon={<FaUser />}><Link to="/signup">SignIn</Link></MenuItem>
            </Menu>
          </SidebarFooter>
        </ProSidebar>
      </div>
    </>
  );
};

export default Sidebar;
