import Data from "./MOCK_DATA.json";
import { React, useState} from "react";

function Hire(){
  const[query, setQuery] = useState("")
return (
<>
    <input className="searchbar" placeholder="Find Talent" 
    onChange={event => setQuery(event.target.value)}/>
    
<div className="hire"> 

    {
Data.filter(function (post) {
    if (query ==='') {
      return post;
    }
    else if (post.first_name.toLowerCase().includes(query.toLowerCase())) {
      return post;
    }

  }).map((post, index) => (
          <div className="hirebox" key={index}>
            <p className="buildimg">Name : {post.first_name} 
            <img src={post.img} height="60" width="60" alt="pic"/></p>
            <p>working at {post.working},</p>
            <p>exp : {post.exp}</p>
            <p>{post.email}</p>
            <button className="ownbutton">Get Resume</button>
          </div>
        ))
      }
      </div>
    </>
  );
}

export default Hire;